import { put } from 'redux-saga/effects'
import * as Interface from '../../_common/interface'
import { getGuid } from '../../_common/api'

export function* setSettings(action) {
  // Set Default Settings
  const data = yield getGuid()
  Interface.setSettings({ ...action.settings })

  yield put({ type: 'SETUP_EXTENSION_COMPLETE', guid: data.guid })
}



