import { all, put, select, call } from 'redux-saga/effects'
import semver from 'semver'

import {
  setSettings,
  getSetting,
  requestPermission
} from '../../_common/interface' // getVersion
import { getArticles } from './articles'
import { getTopics } from './topics'
import { requestTopSites, topSiteSet } from './topsites'
import { requestBookmarks, bookmarksSet } from './bookmarks'
import { searchBarSet } from './search'
import { darkModeSet } from './darkmode'

export const getStoredRecs = state => state.newtab.recs
export const getStoredTopics = state => state.newtab.topics
export const getCacheTime = state => state.newtab.cacheTime
export const getSearchBarActive = state => state.newtab.searchBarActive
export const getBookmarksActive = state => state.newtab.bookmarksActive
export const getTopSitesActive = state => state.newtab.topSitesActive

const CACHE_INTERVAL = 900000
const PANEL_VERSION = '1.2.1'

export function* getCached() {
  const cacheTime = yield select(getCacheTime)

  if (!cacheTime)
    return { recs: [], topics: [], cacheTime: Date.now(), isCached: false }

  const sendCached = Date.now() - cacheTime < CACHE_INTERVAL

  if (sendCached) {
    const storedRecs = yield select(getStoredRecs)
    const storedTopics = yield select(getStoredTopics)

    return Object.keys(storedRecs).length
      ? {
          recs: storedRecs,
          topics: storedTopics,
          cacheTime: cacheTime,
          isCached: true
        }
      : { recs: [], topics: [], cacheTime: Date.now(), isCached: false }
  }

  return { recs: [], topics: [], cacheTime: Date.now(), isCached: false }
}

export function* newTabOpened() {
  // Rehydrate State / Set Defaults
  const settings = yield call(getStoredState)
  yield put({ type: 'NEWTAB_SETTINGS', ...settings })

  const showPanel = yield call(checkVersion)

  if (showPanel.shouldShow) {
    yield put({ type: 'INFOPANEL_ACTIVE', isUpdate: showPanel.isUpdate })
  }

  yield call(buildNewtab, settings)
}

export function* buildNewtab(settings) {
  if (settings.topSitesAccess && settings.topSitesActive)
    yield requestTopSites()
  if (settings.bookmarksAccess && settings.bookmarksActive)
    yield requestBookmarks()

  const cachedData = yield getCached()

  const [recs, topics] = yield all([
    call(getArticles, cachedData),
    call(getTopics, cachedData)
  ])

  yield put({ type: 'ANALYTICS_TAB_OPENED', data: { recs, topics } })
}

export function checkVersion() {
  const storedVersion = getSetting('base_panel_version')

  if (storedVersion == null) {
    const now = Date.now()
    const isUpdate = now < 1507100400000 // October, 4th 2017: Remove this on next update
    return { shouldShow: true, isUpdate }
  }

  return {
    shouldShow: semver.neq(storedVersion, PANEL_VERSION),
    isUpdate: true
  }
}

export function getStoredState() {
  const bookmarksActive = getSetting('newtab_bookmarksActive')
  const bookmarksAccess = getSetting('newtab_bookmarksAccess')
  const topSitesAccess = getSetting('newtab_topSitesAccess')
  const topSitesActive = getSetting('newtab_topSitesActive')
  const searchBarActive = getSetting('newtab_searchBarActive')
  const darkModeActive = getSetting('newtab_darkModeActive')

  if (bookmarksActive == null) setSettings({ newtab_bookmarksActive: 1 })
  if (bookmarksAccess == null) setSettings({ newtab_bookmarksAccess: 0 })
  if (topSitesAccess == null) setSettings({ newtab_topSitesAccess: 0 })
  if (topSitesActive == null) setSettings({ newtab_topSitesActive: 1 })
  if (searchBarActive == null) setSettings({ newtab_searchBarActive: 1 })
  if (darkModeActive == null) setSettings({ newtab_darkModeActive: 0 })

  return {
    bookmarksActive:
      bookmarksActive == null ? true : parseInt(bookmarksActive, 10) === 1,
    bookmarksAccess:
      bookmarksAccess == null ? false : parseInt(bookmarksAccess, 10) === 1,
    topSitesAccess:
      topSitesAccess == null ? false : parseInt(topSitesAccess, 10) === 1,
    topSitesActive:
      topSitesActive == null ? true : parseInt(topSitesActive, 10) === 1,
    searchBarActive:
      searchBarActive == null ? true : parseInt(searchBarActive, 10) === 1,
    darkModeActive:
      darkModeActive == null ? false : parseInt(darkModeActive, 10) === 1
  }
}

export function* featuresSelected(actions) {
  yield all([
    call(bookmarksSet, actions.data.bookmarks),
    call(topSiteSet, actions.data.topSites),
    call(searchBarSet, actions.data.search)
  ])

  const permissionsArray = Object.keys(actions.data).filter(key => {
    return actions.data[key] && key !== 'search' && key !== 'darkMode'
      ? key
      : false
  })

  if (permissionsArray.length) {
    const permissionGranted = yield requestPermission(permissionsArray)

    if (actions.data.topSites) {
      if (permissionGranted) {
        yield requestTopSites()
        yield put({ type: 'ANALYTICS_TOP_SITES_PERMISSION_GRANT' })
      } else {
        yield put({ type: 'ANALYTICS_TOP_SITES_PERMISSION_DENY' })
      }
    }

    if (actions.data.bookmarks) {
      if (permissionGranted) {
        yield requestBookmarks()
        yield put({ type: 'ANALYTICS_BOOKMARKS_PERMISSION_GRANT' })
      } else {
        yield put({ type: 'ANALYTICS_BOOKMARKS_PERMISSION_DENY' })
      }
    }
  }

  yield call(darkModeSet, actions.data.darkMode)
  setSettings({ base_panel_version: PANEL_VERSION })
  yield put({ type: 'FEATURES_COMPLETE' })
}



