import { put, select } from 'redux-saga/effects'

import {
  topSites,
  checkPermission,
  requestPermission,
  removePermission,
  setSettings
} from '../../_common/interface'
import { domainForUrl, isValidAddress } from '../../_common/utilities'

export const getTopSitesAccess = state => state.newtab.topSitesAccess
export const getTopSitesActive = state => state.newtab.topSitesActive
export const getTopSitesVerbose = state => state.newtab.topSitesVerbose
export const getStoredTopsites = state => state.newtab.topSites

export function* requestTopSitesPermission() {
  const hasPermission = yield checkPermission(['topSites'])
  if (hasPermission) return true

  const permissionGranted = yield requestPermission(['topSites'])

  const analyticsType = permissionGranted
    ? 'ANALYTICS_TOP_SITES_PERMISSION_GRANT'
    : 'ANALYTICS_TOP_SITES_PERMISSION_DENY'

  yield put({ type: analyticsType })

  return permissionGranted
}

export function* requestTopSites() {
  yield put({ type: 'NEWTAB_TOP_SITE_REQUEST' })

  const active = yield select(getTopSitesActive)
  const permission = yield requestTopSitesPermission()

  if (permission && active) {
    setSettings({ newtab_topSitesAccess: 1 })
    setSettings({ newtab_topSitesActive: 1 })

    const data = yield topSites()
    const filteredData = data
      .map(item => {
        if (!isValidAddress(item.url)) return false

        item.domain = domainForUrl(item.url)
        return item
      })
      .filter(value => value)

    yield put({ type: 'TOP_SITE_SUCCESS', data: filteredData })
    yield put({ type: 'TOP_SITE_SUCCESS', data: filteredData })
  } else {
    // Active is the default.  If we are denied
    // permissions we should turn it off
    if (!permission && active) yield put({ type: 'TOP_SITE_TOGGLE' })
  }
}

export function* topSiteToggle() {
  // const permision = yield checkPermission(['topSites'])
  const active = yield select(getTopSitesActive)
  setSettings({ newtab_topSitesActive: active ? 0 : 1 })
  yield put({ type: 'TOP_SITE_TOGGLE_SUCCESS', active: !active })

  const topSites = yield select(getStoredTopsites)
  if (!active && !topSites.length) yield requestTopSites()

  const analyticsType = !active
    ? 'ANALYTICS_TOP_SITES_SHOW'
    : 'ANALYTICS_TOP_SITES_HIDE'

  yield put({ type: analyticsType })
}

export function* topSiteRemovePermission() {
  yield removePermission(['topSites'])
  setSettings({ newtab_topSitesActive: 0, newtab_topSitesAccess: 0 })
  yield put({ type: 'TOP_SITE_REMOVE_PERMISSIONS_SUCCESS' })
}

export function* topSiteSet(value) {
  setSettings({ newtab_topSitesActive: value ? 1 : 0 })
  yield put({ type: 'TOP_SITE_TOGGLE_SUCCESS', active: value })

  const analyticsType = value
    ? 'ANALYTICS_TOP_SITES_SHOW'
    : 'ANALYTICS_TOP_SITES_HIDE'

  yield put({ type: analyticsType, infoPanel: true })
}

export function* topSiteVerboseToggle() {
  console.log('Here')
  const verbose = yield select(getTopSitesVerbose)
  console.log(verbose)

  setSettings({ newtab_topSitesVerbose: verbose ? 0 : 1 })
  yield put({ type: 'TOP_SITE_VERBOSE_TOGGLE_SUCCESS', verbose: !verbose })
}



