import { delay } from 'redux-saga'
import { sendAnalytics, getGuid } from '../../../_common/api'

export function* analyticsBookmarksPermissionGrant() {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'bookmarks_bar',
        identifier: 'permission_accept'
      }
    ],
    guidData.guid
  )
}

export function* analyticsBookmarksPermissionDeny() {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'bookmarks_bar',
        identifier: 'permission_deny'
      }
    ],
    guidData.guid
  )
}

export function* analyticsBookmarksShow(action) {
  yield delay(2000)

  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'bookmarks_bar',
        identifier: 'show',
        cxt_intro_ind: action.infoPanel ? 1 : 0
      }
    ],
    guidData.guid
  )
}

export function* analyticsBookmarksHide(action) {
  yield delay(2000)

  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'bookmarks_bar',
        identifier: 'hide',
        cxt_intro_ind: action.infoPanel ? 1 : 0
      }
    ],
    guidData.guid
  )
}



