import { createStore, applyMiddleware, compose } from 'redux'
import { createLogger } from 'redux-logger'
import { wrapStore } from 'react-chrome-redux'
import createSagaMiddleware from 'redux-saga'
import { getSetting } from './_common/interface'

import rootSaga from './sagas'
import rootReducer from './reducers'

import { bindActionCreators } from 'redux'
import * as Actions from './actions'

export function mapDispatchToProps(dispatch) {
  return bindActionCreators(Actions, dispatch)
}

const loglevel = getSetting('base_loglevel')

const omitState = (getState, action) => {
  if (loglevel === 'LOUD') return true
  if (loglevel === 'DEFAULT')
    return (
      action.type !== 'BOOKMARKS_NODE_OPEN' &&
      action.type !== 'BOOKMARKS_SUB_SUCCESS' &&
      action.type !== 'BOOKMARKS_OVERFLOW'
    )

  return false
}

/* SETTINGS
–––––––––––––––––––––––––––––––––––––––––––––––––– */
const logger_settings = {
  level: 'log',
  logger: console,
  logErrors: true,
  predicate: omitState,
  collapsed: (getState, action, logEntry) => !logEntry.error,
  duration: true,
  timestamp: true,
  stateTransformer: state => state,
  actionTransformer: action => action,
  errorTransformer: error => error,
  colors: {
    title: () => '#50BCB6',
    prevState: () => '#ef4056',
    action: () => '#FCB643',
    nextState: () => '#83EDB8',
    error: () => '#FF007F'
  },
  diff: false
}

const logger = createLogger(logger_settings) //
const sagaMiddleware = createSagaMiddleware()
const enhancers = compose(applyMiddleware(sagaMiddleware, logger))

export const store = createStore(rootReducer, {}, enhancers)

wrapStore(store, { portName: 'PKT_EXT' })

sagaMiddleware.run(rootSaga)



