import { getSlug } from '../../../_common/helpers'
import { sendAnalytics, getGuid } from '../../../_common/api'

export function* analyticsClickTrendingTopic(actions) {
  const slug = getSlug(actions.data.item.url)
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'newtab',
        identifier: 'topic_click',
        cxt_index: actions.data.index,
        cxt_slug: slug
      }
    ],
    guidData.guid
  )
}

export function* analyticsClickViewMore() {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'newtab',
        identifier: 'view_more_stories'
      }
    ],
    guidData.guid
  )
}



