import { put, select } from 'redux-saga/effects'
import { setSettings } from '../../_common/interface'

export const getSearchBarActive = state => state.newtab.searchBarActive
export function* searchBarToggle() {
  const active = yield select(getSearchBarActive)
  setSettings({ newtab_searchBarActive: active ? 0 : 1 })
  yield put({ type: 'SEARCH_BAR_TOGGLE_SUCCESS', active: !active })

  const analyticsType = !active
    ? 'ANALYTICS_SEARCH_SHOW'
    : 'ANALYTICS_SEARCH_HIDE'

  yield put({ type: analyticsType })
}

export function* searchBarSet(value) {
  setSettings({ newtab_searchBarActive: value ? 1 : 0 })
  yield put({ type: 'SEARCH_BAR_TOGGLE_SUCCESS', active: value })

  const analyticsType = value
    ? 'ANALYTICS_SEARCH_SHOW'
    : 'ANALYTICS_SEARCH_HIDE'

  yield put({ type: analyticsType, infoPanel: true })
}



