import { sendAnalytics, getGuid } from '../../../_common/api'

export function* analyticsRecClicked(actions) {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'newtab',
        identifier: 'rec_click',
        cxt_item_id: actions.data.id,
        cxt_index: actions.data.index + 1 // switch from 0 base
      }
    ],
    guidData.guid
  )
}

export function* analyticsRecSaved(actions) {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'newtab',
        identifier: 'rec_save',
        cxt_item_id: actions.data.id,
        cxt_index: actions.data.index + 1 // switch from 0 base
      }
    ],
    guidData.guid
  )
}



