import { delay } from 'redux-saga'
import { sendAnalytics, getGuid } from '../../../_common/api'

export function* analyticsTopSitesPermissionGrant() {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'top_sites',
        identifier: 'permission_accept'
      }
    ],
    guidData.guid
  )
}

export function* analyticsTopSitesPermissionDeny() {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'top_sites',
        identifier: 'permission_deny'
      }
    ],
    guidData.guid
  )
}

export function* analyticsTopSitesShow(action) {
  yield delay(2000)

  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'top_sites',
        identifier: 'show',
        cxt_intro_ind: action.infoPanel ? 1 : 0
      }
    ],
    guidData.guid
  )
}

export function* analyticsTopSitesHide(action) {
  yield delay(2000)

  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'top_sites',
        identifier: 'hide',
        cxt_intro_ind: action.infoPanel ? 1 : 0
      }
    ],
    guidData.guid
  )
}

export function* analyticsTopSitesClicked() {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'top_sites',
        identifier: 'topic_click'
      }
    ],
    guidData.guid
  )
}



