import { delay } from 'redux-saga'
import { sendAnalytics, getGuid } from '../../../_common/api'

export function* analyticsDarkModeOn(action) {
  yield delay(2000)

  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'dark_mode',
        identifier: 'on',
        cxt_intro_ind: action.infoPanel ? 1 : 0
      }
    ],
    guidData.guid
  )
}

export function* analyticsDarkModeOff(action) {
  yield delay(2000)

  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'dark_mode',
        identifier: 'off',
        cxt_intro_ind: action.infoPanel ? 1 : 0
      }
    ],
    guidData.guid
  )
}



