import { sendAnalytics, getGuid } from '../../../_common/api'

export function* analyticsTabOpened(action) {
  const guidData = yield getGuid()

  const itemIds = Object.keys(action.data.recs)
    .map(x => action.data.recs[x])
    .reduce((previous, current) => {
      return {
        ...previous,
        [`cxt_imp${current.index + 1}_item_id`]: current.id
      }
    }, {})

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'newtab',
        identifier: 'displayed',
        cxt_recs_cnt: Object.keys(action.data.recs).length,
        cxt_topics_cnt: action.data.topics.length,
        ...itemIds
      }
    ],
    guidData.guid
  )
}

export function* analyticsUninstall() {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'settings',
        identifier: 'uninstall'
      }
    ],
    guidData.guid
  )
}

export function* analyticsClickPocket() {
  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'newtab',
        identifier: 'click_via_pocket'
      }
    ],
    guidData.guid
  )
}



