import { call, put, race } from 'redux-saga/effects'
import { delay } from 'redux-saga'
import { getTrendingTopics, getGuid } from '../../_common/api'

export function* getTopics(cachedData) {
  const validCache =
    cachedData.isCached && !!Object.keys(cachedData.topics).length
  if (validCache) return yield sendCached(cachedData)

  try {
    const guid = yield getGuid()
    const { responseData } = yield race({
      responseData: call(getTrendingTopics, guid),
      timeout: call(delay, 30000)
    })

    const data = responseData ? responseData[0].response.topics : []
    yield put({ type: 'NEWTAB_TRENDING_SUCCESS', data })

    return data
  } catch (error) {
    yield put({ type: 'NEWTAB_TRENDING_FAILURE', data: [] })

    return []
  }
}

function* sendCached(cachedData) {
  const data = cachedData.topics
  yield put({ type: 'NEWTAB_TRENDING_CACHED', data })

  return data
}



