import { put, select, call } from 'redux-saga/effects'
import {
  bookmark,
  bookmarksChildren,
  checkPermission,
  requestPermission,
  setSettings
} from '../../_common/interface'

export const getbookmarksAccess = state => state.newtab.bookmarksAccess
export const getBookmarksActive = state => state.newtab.bookmarksActive
export const getStoredbookmarks = state => state.newtab.bookmarks

export function* requestBookmarksPermission() {
  const hasPermission = yield checkPermission(['bookmarks'])
  if (hasPermission) return true

  // yield put({type: 'ANALYTICS_BOOKMARK_PERMISSION_REQUEST'})
  const permissionGranted = yield requestPermission(['bookmarks'])

  const analyticsType = permissionGranted
    ? 'ANALYTICS_BOOKMARKS_PERMISSION_GRANT'
    : 'ANALYTICS_BOOKMARKS_PERMISSION_DENY'

  yield put({ type: analyticsType })
  return permissionGranted
}

export function* requestBookmarks() {
  yield put({ type: 'NEWTAB_BOOKMARKS_REQUEST' })

  const active = yield select(getBookmarksActive)
  const permission = yield requestBookmarksPermission()

  if (permission && active) {
    setSettings({ newtab_bookmarksAccess: 1 })
    setSettings({ newtab_bookmarksActive: 1 })

    const data = yield call(bookmarksChildren, '1')
    yield put({ type: 'BOOKMARKS_SUCCESS', data: data })
  } else {
    // Active is the default.  If we are denied
    // permissions we should turn it off
    if (!permission && active) yield put({ type: 'BOOKMARKS_TOGGLE' })
  }
}

export function* requestBookmarksOpen(action) {
  yield put({ type: 'BOOKMARKS_NODE_CLEAR' })

  const list = yield bookmarksChildren(action.data.item.id)
  yield put({
    type: 'BOOKMARKS_SUB_SUCCESS',
    list,
    parent: action.data.item,
    barId: action.data.barId
  })
}

export function* requestBookmarksClose(action) {
  const current = yield select(getStoredbookmarks)

  const list = yield bookmarksChildren(current.secondary.parent.parentId)
  const parent = yield bookmark(list[0].parentId)

  yield put({
    type: 'BOOKMARKS_SUB_SUCCESS',
    list,
    parent: parent[0],
    barId: action.data.barId
  })
}

export function* bookmarksToggle() {
  // const permision = yield checkPermission(['bookmarks'])
  const active = yield select(getBookmarksActive)
  setSettings({ newtab_bookmarksActive: active ? 0 : 1 })
  yield put({ type: 'BOOKMARKS_TOGGLE_SUCCESS', active: !active })

  const bookmarks = yield select(getStoredbookmarks)
  if (!active && !bookmarks.length) yield requestBookmarks()

  const analyticsType = !active
    ? 'ANALYTICS_BOOKMARKS_SHOW'
    : 'ANALYTICS_BOOKMARKS_HIDE'

  yield put({ type: analyticsType })
}

export function* bookmarksSet(value) {
  setSettings({ newtab_bookmarksActive: value ? 1 : 0 })
  yield put({ type: 'BOOKMARKS_TOGGLE_SUCCESS', active: value })

  const analyticsType = value
    ? 'ANALYTICS_BOOKMARKS_SHOW'
    : 'ANALYTICS_BOOKMARKS_HIDE'

  yield put({ type: analyticsType, infoPanel: true })
}



