import { put, select } from 'redux-saga/effects'
import { setSettings } from '../../_common/interface'

export const getDarkModeActive = state => state.newtab.darkModeActive
export function* darkModeToggle() {
  const active = yield select(getDarkModeActive)
  setSettings({ newtab_darkModeActive: active ? 0 : 1 })
  yield put({ type: 'DARK_MODE_TOGGLE_SUCCESS', active: !active })

  const analyticsType = !active
    ? 'ANALYTICS_DARK_MODE_ON'
    : 'ANALYTICS_DARK_MODE_OFF'

  yield put({ type: analyticsType })
}

export function* darkModeSet(value) {
  setSettings({ newtab_darkModeActive: value ? 1 : 0 })
  yield put({ type: 'DARK_MODE_TOGGLE_SUCCESS', active: value })

  const analyticsType = value
    ? 'ANALYTICS_DARK_MODE_ON'
    : 'ANALYTICS_DARK_MODE_OFF'

  yield put({ type: analyticsType, infoPanel: true })
}



