import { all, takeLatest, takeEvery } from 'redux-saga/effects'
import { setSettings } from './settings'
import { openPocket } from '../_common/helpers'
import { verifyAuthCode, handleLogOut } from './auth/auth'

import { newTabOpened, featuresSelected } from './newtab'
import { saveArticleRequest, removeArticleRequest } from './newtab/articles'
import { searchBarToggle } from './newtab/search'
import { darkModeToggle } from './newtab/darkmode'
import {
  bookmarksToggle,
  requestBookmarks,
  requestBookmarksOpen,
  requestBookmarksClose
} from './newtab/bookmarks'

import {
  requestTopSites,
  topSiteToggle,
  topSiteRemovePermission,
  topSiteVerboseToggle
} from './newtab/topsites'

import {
  analyticsTabOpened,
  analyticsUninstall,
  analyticsClickPocket
} from './analytics/newtab'

import {
  analyticsDarkModeOn,
  analyticsDarkModeOff
} from './analytics/newtab/darkmode'

import { analyticsRecClicked, analyticsRecSaved } from './analytics/newtab/recs'

import {
  analyticsSearchShow,
  analyticsSearchHide
} from './analytics/newtab/search'

import {
  analyticsTopSitesPermissionGrant,
  analyticsTopSitesPermissionDeny,
  analyticsTopSitesShow,
  analyticsTopSitesHide,
  analyticsTopSitesClicked
} from './analytics/newtab/topsites'

import {
  analyticsBookmarksPermissionGrant,
  analyticsBookmarksPermissionDeny,
  analyticsBookmarksShow,
  analyticsBookmarksHide
} from './analytics/newtab/bookmarks'

import {
  analyticsClickTrendingTopic,
  analyticsClickViewMore
} from './analytics/newtab/trending'

/* WATCHERS
–––––––––––––––––––––––––––––––––––––––––––––––––– */
// If this is the initial install, we store some default settings
function* watchSetup() {
  yield takeLatest('SETUP_EXTENSION', setSettings)
}

// When logging in we need to listen for an AUTH code from the logged in success page
function* watchAuthCodeRecieved() {
  yield takeEvery('AUTH_CODE_RECIEVED', verifyAuthCode)
}

function* watchLogout() {
  yield takeLatest('USER_LOGOUT', handleLogOut)
}

// General
function* watchOpenPocketRequest() {
  yield takeLatest('OPEN_POCKET', openPocket)
}

// Newtab
function* watchOpenNewTab() {
  yield takeLatest('NEWTAB_OPENED', newTabOpened)
}

function* watchFeaturesSelected() {
  yield takeLatest('FEATURES_SELECTED', featuresSelected)
}

function* watchTopSitesRequest() {
  yield takeLatest('TOP_SITE_REQUEST', requestTopSites)
}

function* watchTopSitesToggle() {
  yield takeLatest('TOP_SITE_TOGGLE', topSiteToggle)
}

function* watchTopSiteVerboseToggle() {
  yield takeLatest('TOP_SITE_VERBOSE_TOGGLE', topSiteVerboseToggle)
}

function* watchTopSitePermission() {
  yield takeLatest(
    'TOP_SITE_REMOVE_PERMISSION_REQUEST',
    topSiteRemovePermission
  )
}

function* watchBookmarksToggle() {
  yield takeLatest('BOOKMARKS_TOGGLE', bookmarksToggle)
}
function* watchBookmarksRequest() {
  yield takeLatest('BOOKMARKS_REQUEST', requestBookmarks)
}
function* watchBookmarksNodeOpen() {
  yield takeLatest('BOOKMARKS_NODE_OPEN', requestBookmarksOpen)
}
function* watchBookmarksNodeClose() {
  yield takeLatest('BOOKMARKS_NODE_CLOSE', requestBookmarksClose)
}

function* watchSearchBarToggle() {
  yield takeLatest('SEARCH_BAR_TOGGLE', searchBarToggle)
}

function* watchDarkModeToggle() {
  yield takeLatest('DARK_MODE_TOGGLE', darkModeToggle)
}

function* watchGlobalRecSave() {
  yield takeEvery('GLOBAL_REC_SAVE_REQUEST', saveArticleRequest)
}

function* watchGlobalRecRemove() {
  yield takeEvery('GLOBAL_REC_REMOVE_REQUEST', removeArticleRequest)
}

// Analytics Settings
function* watchAnalyticsTabOpened() {
  yield takeLatest('ANALYTICS_TAB_OPENED', analyticsTabOpened)
}
function* watchAnalyticsUninstall() {
  yield takeLatest('ANALYTICS_UNINSTALL', analyticsUninstall)
}

function* watchAnalyticsClickPocket() {
  yield takeLatest('ANALYTICS_CLICK_POCKET', analyticsClickPocket)
}

function* watchAnalyticsDarkModeOn() {
  yield takeLatest('ANALYTICS_DARK_MODE_ON', analyticsDarkModeOn)
}
function* watchAnalyticsDarkModeOff() {
  yield takeLatest('ANALYTICS_DARK_MODE_OFF', analyticsDarkModeOff)
}

// Analytics Recommendations
function* watchAnalyticsRecClicked() {
  yield takeLatest('ANALYTICS_REC_CLICKED', analyticsRecClicked)
}
function* watchAnalyticsRecSaved() {
  yield takeLatest('ANALYTICS_REC_SAVED', analyticsRecSaved)
}

// Analytics Search
function* watchAnalyticsSearchShow() {
  yield takeLatest('ANALYTICS_SEARCH_SHOW', analyticsSearchShow)
}
function* watchAnalyticsSearchHide() {
  yield takeLatest('ANALYTICS_SEARCH_HIDE', analyticsSearchHide)
}

// Analytics Top Sites
function* watchAnalyticsTopSitesPermissionGrant() {
  yield takeLatest(
    'ANALYTICS_TOP_SITES_PERMISSION_GRANT',
    analyticsTopSitesPermissionGrant
  )
}
function* watchAnalyticsTopSitesPermissionDeny() {
  yield takeLatest(
    'ANALYTICS_TOP_SITES_PERMISSION_DENY',
    analyticsTopSitesPermissionDeny
  )
}

function* watchAnalyticsTopSitesShow() {
  yield takeLatest('ANALYTICS_TOP_SITES_SHOW', analyticsTopSitesShow)
}
function* watchAnalyticsTopSitesHide() {
  yield takeLatest('ANALYTICS_TOP_SITES_HIDE', analyticsTopSitesHide)
}

function* watchAnalyticsTopSitesClicked() {
  yield takeLatest('ANALYTICS_TOP_SITES_CLICK', analyticsTopSitesClicked)
}

// Analytics Bookmarks Bar
function* watchAnalyticsBookmarksPermissionGrant() {
  yield takeLatest(
    'ANALYTICS_BOOKMARKS_PERMISSION_GRANT',
    analyticsBookmarksPermissionGrant
  )
}
function* watchAnalyticsBookmarksPermissionDeny() {
  yield takeLatest(
    'ANALYTICS_BOOKMARKS_PERMISSION_DENY',
    analyticsBookmarksPermissionDeny
  )
}
function* watchAnalyticsBookmarksShow() {
  yield takeLatest('ANALYTICS_BOOKMARKS_SHOW', analyticsBookmarksShow)
}
function* watchAnalyticsBookmarksHide() {
  yield takeLatest('ANALYTICS_BOOKMARKS_HIDE', analyticsBookmarksHide)
}

// Analytics Topics
function* watchAnalyticsClickTrending() {
  yield takeLatest(
    'ANALYTICS_TRENDING_TOPIC_CLICK',
    analyticsClickTrendingTopic
  )
}
function* watchAnalyticsClickViewMore() {
  yield takeLatest('ANALYTICS_VIEW_MORE_CLICK', analyticsClickViewMore)
}

export default function* rootSaga() {
  yield all([
    watchAnalyticsBookmarksHide(),
    watchAnalyticsBookmarksPermissionDeny(),
    watchAnalyticsBookmarksPermissionGrant(),
    watchAnalyticsBookmarksShow(),
    watchAnalyticsClickPocket(),
    watchAnalyticsClickTrending(),
    watchAnalyticsClickViewMore(),
    watchAnalyticsDarkModeOff(),
    watchAnalyticsDarkModeOn(),
    watchAnalyticsRecClicked(),
    watchAnalyticsRecSaved(),
    watchAnalyticsSearchHide(),
    watchAnalyticsSearchShow(),
    watchAnalyticsTabOpened(),
    watchAnalyticsTopSitesClicked(),
    watchAnalyticsTopSitesHide(),
    watchAnalyticsTopSitesPermissionDeny(),
    watchAnalyticsTopSitesPermissionGrant(),
    watchAnalyticsTopSitesShow(),
    watchAnalyticsUninstall(),
    watchAuthCodeRecieved(),
    watchBookmarksNodeClose(),
    watchBookmarksNodeOpen(),
    watchBookmarksRequest(),
    watchBookmarksToggle(),
    watchDarkModeToggle(),
    watchFeaturesSelected(),
    watchGlobalRecRemove(),
    watchGlobalRecSave(),
    watchLogout(),
    watchOpenNewTab(),
    watchOpenPocketRequest(),
    watchSearchBarToggle(),
    watchSetup(),
    watchTopSitePermission(),
    watchTopSitesRequest(),
    watchTopSitesToggle(),
    watchTopSiteVerboseToggle()
  ])
}



