import { delay } from 'redux-saga'
import { sendAnalytics, getGuid } from '../../../_common/api'

export function* analyticsSearchShow(action) {
  yield delay(2000)

  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'search_bar',
        identifier: 'show',
        cxt_intro_ind: action.infoPanel ? 1 : 0
      }
    ],
    guidData.guid
  )
}

export function* analyticsSearchHide(action) {
  yield delay(2000)

  const guidData = yield getGuid()

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'search_bar',
        identifier: 'hide',
        cxt_intro_ind: action.infoPanel ? 1 : 0
      }
    ],
    guidData.guid
  )
}



