import { call, put, race } from 'redux-saga/effects'
import { delay } from 'redux-saga'
import { requireAuthorization } from '../auth/auth'
import {
  saveToPocket,
  getTrendingArticles,
  getGuid,
  removeItem
} from '../../_common/api'

function getBestImage(item) {
  return item.top_image_url
    ? item.top_image_url
    : item.images[Object.keys(item.images)[0]].src
}

function* sendSuccess(list, cachedData) {
  const formattedList = formatArticleResponse(list)
  yield put({
    type: 'NEWTAB_RECOMMENDATION_SUCCESS',
    list: formattedList,
    cacheTime: cachedData.cacheTime,
    isCached: cachedData.isCached
  })

  return formattedList
}

function* sendError(pauseTime) {
  yield call(delay, pauseTime)
  yield put({ type: 'NEWTAB_RECOMMENDATION_FAILURE' })

  return {}
}

function* sendCached(cachedData) {
  yield put({
    type: 'NEWTAB_RECOMMENDATION_CACHED',
    list: cachedData.recs,
    cacheTime: cachedData.cacheTime,
    isCached: cachedData.isCached
  })

  return cachedData.recs
}

export function* getArticles(cachedData) {
  const validCache =
    cachedData.isCached && !!Object.keys(cachedData.recs).length
  if (validCache) return yield sendCached(cachedData)

  try {
    const guid = yield getGuid()
    const { responseData } = yield race({
      responseData: call(getTrendingArticles, guid, 3),
      timeout: call(delay, 30000)
    })

    if (!responseData[0].response) return yield sendError(10000)
    return yield sendSuccess(responseData[0].response.list, cachedData)
  } catch (error) {
    return yield sendError(10000)
  }
}

function formatArticleResponse(list) {
  return list.reduce((map, obj, index) => {
    const recObject = {
      index: index,
      id: obj.item_id,
      has_image: obj.has_image,
      title: obj.title,
      url: obj.resolved_url,
      redirect_url: obj.redirect_url,
      excerpt: obj.excerpt,
      image: getBestImage(obj),
      status: 'idle'
    }

    map[obj.item_id] = recObject

    return map
  }, {})
}

export function* saveArticleRequest(action) {
  yield put({ type: 'SAVE_GLOBAL_REC_TO_POCKET', id: action.data.id })

  const { authToken } = yield race({
    authToken: call(requireAuthorization),
    timeout: call(delay, 10000)
  })

  if (authToken) {
    const guidData = yield getGuid()
    const data = yield call(
      saveToPocket,
      {
        url: action.data.url,
        title: action.data.title
      },
      authToken,
      guidData.guid
    )

    const analyticsObject = { id: action.data.id, index: action.data.index }
    if (data && data.status === 'ok') {
      yield put({ type: 'ANALYTICS_REC_SAVED', data: analyticsObject })
      yield put({
        type: 'SAVE_GLOBAL_REC_TO_POCKET_SUCCESS',
        data,
        id: action.data.id
      })
    } else {
      yield put({
        type: 'SAVE_GLOBAL_REC_TO_POCKET_FAILURE',
        status: 'not ok',
        id: action.data.id
      })
    }
  } else {
    yield put({
      type: 'SAVE_GLOBAL_REC_TO_POCKET_FAILURE',
      status: 'timeout',
      id: action.data.id
    })
  }
}

export function* removeArticleRequest(action) {
  yield put({ type: 'REMOVE_GLOBAL_REC_REQUEST', id: action.data.id })

  const { authToken, timeout } = yield race({
    authToken: call(requireAuthorization),
    timeout: call(delay, 10000)
  })

  if (authToken) {
    const data = yield call(removeItem, authToken, action.data)
    // const analyticsObject = {id:action.data.id, index:action.data.index}
    // if(data.status === 'ok') yield put({type: 'ANALYTICS_REC_REMOVED', data:analyticsObject})

    yield put({
      type: 'REMOVE_GLOBAL_REC_REQUEST_SUCCESS',
      data,
      id: action.data.id
    })

    yield call(delay, 2500)

    yield put({ type: 'RESET_GLOBAL_REC', id: action.data.id })
  } else {
    yield put({
      type: 'REMOVE_GLOBAL_REC_REQUEST_FAILURE',
      timeout,
      id: action.data.id
    })
  }
}



