import * as Interface from './_common/interface'
import { getBaseUrl } from './_common/helpers'
import { sendAnalytics } from './_common/api'

import { store } from './store'
import * as Actions from './actions'

/* INITIALIZE STATE
–––––––––––––––––––––––––––––––––––––––––––––––––– */
const installed = Interface.getSetting('base_installed')

function openURL() {
  Interface.openTabWithUrl(getBaseUrl())
}

function createContextMenus() {
  Interface.contextMenus().create({
    title: 'Open New Tab',
    contexts: ['page_action'],
    onclick: Interface.openTab
  })

  Interface.contextMenus().create({
    title: 'Open My Pocket List',
    contexts: ['page_action'],
    onclick: openURL
  })
}

Interface.contextMenus().removeAll(createContextMenus)

if (!installed) {
  const defaultSettings = store.getState().settings
  store.dispatch(Actions.setupExtension(defaultSettings))
  Interface.openTab()
}

Interface.onSuspend().addListener(() => {
  const guid = Interface.getSetting('id_guid')

  sendAnalytics(
    [
      {
        view: 'extension',
        section: 'settings',
        identifier: 'uninstall'
      }
    ],
    guid
  )
})

Interface.onUpdateAvailable(() => {
  Interface.reloadExtension()
})

Interface.setUninstallUrl('https://getpocket.com/chrome-new-tab-exit-survey/')



