import { getDefaultKeyboardShortCut } from '../../_common/helpers'

const initialState = {
  base_api_version: 'v3/',
  base_keyboard_shortcut: getDefaultKeyboardShortCut(),
  base_loglevel: 'SILENT',
  base_premium: 1,
  base_URL: 'https://getpocket.com/',
  base_installed: 1,

  on_save_recommendations: 1,

  sites_facebook: 1,
  sites_hackernews: 1,
  sites_reddit: 1,
  sites_twitter: 1
}

export default (state = initialState, action) => {
  switch (action.type) {
    case 'SETUP_EXTENSION_COMPLETE': {
      return {
        ...state,
        guid: action.guid
      }
    }

    default: {
      return state
    }
  }
}



