const defaultState = {
  recs: {},
  topics: [],
  topSites: [],
  bookmarks: {},
  topSitesAccess: false,
  topSitesActive: true,
  topSitesVerbose: false,
  searchBarActive: true,
  bookmarksAccess: false,
  bookmarksActive: false,
  darkModeActive: false,
  infoPanelType: false
}

export default (state = defaultState, action) => {
  switch (action.type) {
    case 'NEWTAB_SETTINGS': {
      return {
        ...state,
        topSitesAccess: action.topSitesAccess,
        topSitesActive: action.topSitesActive,
        searchBarActive: action.searchBarActive,
        bookmarksAccess: action.bookmarksAccess,
        bookmarksActive: action.bookmarksActive,
        darkModeActive: action.darkModeActive,
        errors: {}
      }
    }

    case 'INFOPANEL_ACTIVE': {
      return {
        ...state,
        infoPanelType: action.isUpdate ? 'update' : 'welcome'
      }
    }

    case 'FEATURES_COMPLETE': {
      return {
        ...state,
        infoPanelType: false
      }
    }

    case 'NEWTAB_RECOMMENDATION_CACHED':
    case 'NEWTAB_RECOMMENDATION_SUCCESS': {
      const emptyList = !!!Object.keys(action.list).length
      if (action.isCached || emptyList) return state

      return {
        ...state,
        recs: action.list,
        cacheTime: action.cacheTime
      }
    }

    case 'NEWTAB_RECOMMENDATION_FAILURE': {
      return {
        ...state,
        errors: {
          recs: true
        }
      }
    }

    case 'NEWTAB_TRENDING_SUCCESS': {
      return {
        ...state,
        topics: action.data
      }
    }

    case 'BOOKMARKS_SUCCESS': {
      return {
        ...state,
        bookmarks: {
          list: action.data,
          visibleList: action.data,
          overFlowList: []
        },
        bookmarksAccess: 1
      }
    }

    case 'BOOKMARKS_OVERFLOW': {
      return {
        ...state,
        bookmarks: {
          ...state.bookmarks,
          secondary: {
            parent: {
              id: '-1',
              dateGroupModified: true,
              title: 'Overflow',
              parentId: '-1'
            },
            list: action.data,
            barId: '-1'
          }
        }
      }
    }

    case 'BOOKMARKS_SUB_SUCCESS': {
      return {
        ...state,
        bookmarks: {
          ...state.bookmarks,
          secondary: {
            parent: action.parent,
            list: action.list,
            barId: action.barId
          }
        }
      }
    }

    case 'BOOKMARKS_NODE_CLEAR': {
      return {
        ...state,
        bookmarks: {
          ...state.bookmarks,
          secondary: {}
        }
      }
    }

    case 'BOOKMARKS_TOGGLE_SUCCESS': {
      return {
        ...state,
        bookmarksActive: action.active
      }
    }

    case 'TOP_SITE_SUCCESS': {
      return {
        ...state,
        topSites: action.data,
        topSitesAccess: 1
      }
    }

    case 'TOP_SITE_TOGGLE_SUCCESS': {
      return {
        ...state,
        topSitesActive: action.active
      }
    }

    case 'TOP_SITE_VERBOSE_TOGGLE_SUCCESS': {
      return {
        ...state,
        topSitesVerbose: action.verbose
      }
    }

    case 'TOP_SITE_REMOVE_PERMISSIONS_SUCCESS': {
      return {
        ...state,
        topSites: [],
        topSitesAccess: 0,
        topSitesActive: false
      }
    }

    case 'SEARCH_BAR_TOGGLE_SUCCESS': {
      return {
        ...state,
        searchBarActive: action.active
      }
    }

    case 'DARK_MODE_TOGGLE_SUCCESS': {
      return {
        ...state,
        darkModeActive: action.active
      }
    }

    case 'SAVE_GLOBAL_REC_TO_POCKET': {
      return {
        ...state,
        recs: {
          ...state.recs,
          [action.id]: {
            ...state.recs[action.id],
            status: 'saving'
          }
        }
      }
    }

    case 'SAVE_GLOBAL_REC_TO_POCKET_SUCCESS': {
      return {
        ...state,
        recs: {
          ...state.recs,
          [action.id]: {
            ...state.recs[action.id],
            status: 'saved'
          }
        }
      }
    }

    case 'REMOVE_GLOBAL_REC_REQUEST': {
      return {
        ...state,
        recs: {
          ...state.recs,
          [action.id]: {
            ...state.recs[action.id],
            status: 'removing'
          }
        }
      }
    }

    case 'REMOVE_GLOBAL_REC_REQUEST_SUCCESS': {
      return {
        ...state,
        recs: {
          ...state.recs,
          [action.id]: {
            ...state.recs[action.id],
            status: 'removed'
          }
        }
      }
    }

    case 'RESET_GLOBAL_REC':
    case 'REMOVE_GLOBAL_REC_REQUEST_FAILURE':
    case 'SAVE_GLOBAL_REC_TO_POCKET_FAILURE': {
      return {
        ...state,
        recs: {
          ...state.recs,
          [action.id]: {
            ...state.recs[action.id],
            status: 'idle'
          }
        }
      }
    }

    default: {
      return state
    }
  }
}



